@function reduce ($percent) {
  @return rgba(mix(#292929, #fff), $percent / 100); }

body {
  display: flex;
  font-size: calc(.6vw + .6em);
  margin: 0;
  min-height: 100vh;
  background: rgb(240, 240, 240); }

.sub {
  color: #cdcdcd; }

.category {
  border-radius: 10px;
  font-size: 14px;
  color: white;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 2px;
  padding-bottom: 2px;
  margin: 3px; }

ul {
  list-style-type: circle; }

@import "anchor";

html {
  background-color: #536298; }

article {
  border-top: 1px solid rgba(148, 148, 148, .08); }

article:first-of-type {
  border-top: none; }

article, body > header, body > footer {
  padding: 1em calc(22% - 4em); }

article a {
  color: rgb(112, 132, 190); }

body > header {
  background-color: #282828;
  color: white; }

body > footer {
  background-color: #fafafa;
  color: gray;
  border-top: 1px solid #f0f0f0; }

header h1 {
  margin-top: .4em;
  margin-bottom: .1em; }

article header {
  text-align: center;
  margin-bottom: 1.6em; }

article footer {
  padding: 1em 0; }

article h1, h2, h3 {
  margin-bottom: 0;
  color: #079992; }

article h1 {
  font-size: calc(1vw + 1em);
  border-bottom: 1px lightgray solid; }

article h2 {
  font-size: calc(.75vw + .75em); }

#post #maintitle {
  font-size: calc(1.1vw + 1.1em);
  border-bottom: 1px lightgray solid; }

table {
  width: 100%; }

nav {
  font-size: calc(.6vw + .8em);
  margin: 1em -0.8em; }

nav a {
  display: inline-block;
  margin: 0 .8em; }

nav #menu a:hover {
  border-bottom: #079992 2px solid; }

nav #menu #selected {
  border-bottom: #079992 2px solid; }

nav, body > footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }

.flag {
  margin-right: 10px; }

.archive {
  h3 {
    margin-top: 0; }
  li {
    list-style-type: none; }
  a {
    font-weight: 300; } }

/* ========== Carousel ========== */

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease; }

.active, .dot:hover {
  background-color: #717171; }
